const winImg = require("url:../../img/star-lose.png");
const loseImg = require("url:../../img/star-win.png");

customElements.define(
    "result-img",
    class ResultImg extends HTMLElement {
        shadow: ShadowRoot;
        result: "win" | "lose";

        constructor() {
            super();
            this.shadow = this.attachShadow({ mode: "open" });
            this.result = this.getAttribute("result") as "win" | "lose";
        }
        connectedCallback() {
            this.render();
        }
        render() {
            const style = document.createElement("style");
            style.innerHTML = `
            *{
                box-sizing: border-box;
            }
            .result{
                width:254px;
                height:259px;
                color:white;
                display: inline;
            }
            .result.win{
                background-image:url(${loseImg});
            }
            .result.lose{
                background-image:url(${winImg});
            }
            .text{
                position: relative;
                text-align: center;
                top: -120px;
                right: 180px;
                font-weight: normal;
                font-family: Odibee Sans;
                font-size: 55px;
                color: #fff;
                display: inline;
                margin: 0;
            }
            `;
            const img = document.createElement("img");
            const h1 = document.createElement("h1");
            h1.className = "text";
            h1.textContent = this.textContent;
            img.classList.add("result");
            if (this.result == "win") {
                img.classList.add("win");
            } else {
                img.classList.add("lose");
            }
            this.shadow.appendChild(style);
            this.shadow.appendChild(img);
            this.shadow.appendChild(h1);
        }
    }
);
